import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { LocalDataSource } from 'ng2-smart-table';
import { CustomRenderDateComponent, CustomRenderObjectComponent } from '../pipes/custom-render.component';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.css']
})
export class NotificationsComponent implements OnInit {

  count: Number;
  source: LocalDataSource;
  settings = {
    columns: {
      contents: {
        title: 'Content',
        filter: false,
        editable: false,
        type: 'custom',
        renderComponent: CustomRenderObjectComponent
      },
      failed: {
        title: 'Failed',
        filter: false,
        editable: false
      },
      successful: {
        title: 'Successful',
        filter: false,
        editable: false
      },
      send_after: {
        title: 'Sent At',
        filter: false,
        editable: false,
        type: 'custom',
        renderComponent: CustomRenderDateComponent
      }
    },
    actions: {
      add : false,
      delete: false,
      edit:false,
      position: 'right'
    },
    attr:{
      id: '_id'
    },
    paper: {
      display: true,
      perPage: 20
    }
  };

  constructor(private authService:AuthService,
              private flashMessagesService: FlashMessagesService) { }

  ngOnInit() {
    this.source = new LocalDataSource();
    this.authService.getNotifications().subscribe(data => {
        this.count = data.total_count;
        this.source.load(data.notifications);
        this.source.setPaging(0,20, true);
      },
      err => {
        this.flashMessagesService.show("Please check your OneSignal App ID and API Key", {
          cssClass: 'alert-danger',
          timeout: 5000
        });
        return false;
      })
  }

}



