import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { LocalDataSource } from 'ng2-smart-table';
import { FlashMessagesService } from 'angular2-flash-messages';
import { CustomRenderPasswordComponent } from '../pipes/custom-render.component';
import { ValidateService } from '../services/validate.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  source: LocalDataSource;
  settings = {
    columns: {
      username: {
        title: 'Username',
        filter: false,
        editable: false
      },
      name: {
        title: 'Name',
        filter: false,
        editable: true
      },
      password: {
        title: 'Password',
        filter: false,
        editable: true,
        type: 'custom',
        renderComponent: CustomRenderPasswordComponent
      },
      createdAt: {
        title: 'Created At',
        filter: false,
        editable: false
      },
    },
    actions: {
      position: 'right',
      edit: false
    },
    attr:{
      id: '_id'
    },
    add:{confirmCreate : true,},
    edit: {
      editButtonContent: 'Edit',
      saveButtonContent: 'Update',
      confirmSave: true,
    },
    paper: {
      display: true,
      perPage: 20
    }
  };

  constructor(private authService:AuthService,
              private validateService: ValidateService,
              private flashMessagesService: FlashMessagesService) { }

  ngOnInit() {
    this.source = new LocalDataSource();
    this.authService.getUsers().subscribe(data => {
      console.log(data);
        this.source.load(data.data);
        this.source.setPaging(0,20, true);
      },
      err => {
        console.log(err);
        return false;
      })
  }

  onDeleteConfirm(event) {
    if (window.confirm('Are you sure you want to delete this user?')) {
      let _id =  event.newData['_id'];
      this.authService.pushNotification(_id)
        .subscribe(data => {
          this.authService.updateData(_id)
            .subscribe(data => {
              if (data.success) {
                this.flashMessagesService.show('Notification has been delivered!', {
                  cssClass: 'alert-success',
                  timeout: 3000
                });
                this.authService.getData().subscribe(data => {
                  this.source.load(data);
                  this.source.setPaging(0, 20, true);
                })
              } else {
                this.flashMessagesService.show("Failed to update Db!", {
                  cssClass: 'alert-danger',
                  timeout: 5000
                })
              }
            }, error => {
              this.flashMessagesService.show("An error has occurred! (db)!", {
                cssClass: 'alert-danger',
                timeout: 5000
              })
            })
        }, error => {
          this.flashMessagesService.show("An error has occurred! Please check API Key, App ID, Template ID.", {
            cssClass: 'alert-danger',
            timeout: 5000
          })
        });

      event.confirm.resolve();
    } else {
      event.confirm.reject();
    }
  }

  onCreateConfirm(event) {
    if (window.confirm('Are you sure you want to create user?')) {
      console.log(event.newData);

      const user = {
        name : event.newData.name,
        username : event.newData.username,
        password : event.newData.password
      };

      //required fields
      if(!this.validateService.validateRegister(user)) {
        this.flashMessagesService.show('Please fill in all fields',{cssClass: 'alert-danger', timeout: 3000})
        return false;
      }

      //register user
      this.authService.registerUser(user)
        .subscribe(data => {
          if(data.success){
            this.flashMessagesService.show('User is created!', {
              cssClass: 'alert-success',
              timeout:3000});
          } else {
            this.flashMessagesService.show(data.msg, {
              cssClass: 'alert-danger',
              timeout:5000});
          }
        })

      event.confirm.resolve();
    } else {
      event.confirm.reject();
    }
  }

}



