import { Component } from '@angular/core';
import { DialogComponent, DialogService } from "ng2-bootstrap-modal";

export interface PromptModel {
  title:string;
  message:string;
}

@Component({
  selector: 'prompt',
  template: `<div class="modal-dialog">
                <div class="modal-content">
                   <div class="modal-header">
                     <button type="button" class="close" (click)="close()">&times;</button>
                     <h4 class="modal-title">New Message</h4>
                   </div>
                   <div class="modal-body">
                    <label>Title</label><input type="text" class="form-control" [(ngModel)]="title" name="title" >
                    <label>Message</label><input type="text" class="form-control" [(ngModel)]="message" name="message" >
                   </div>
                   <div class="modal-footer">
                     <button type="button" class="btn btn-default" (click)="close()" >Cancel</button>
                     <button type="button" class="btn btn-primary" (click)="apply()">Send</button>
                   </div>
                 </div>
                </div>`
})
export class PromptComponent extends DialogComponent<PromptModel, string> implements PromptModel {
  title: string = '';
  message: string = '';
  result: any;
  constructor(dialogService: DialogService) {
    super(dialogService);
  }
  apply() {
    this.result = {title: this.title, message: this.message};
    this.close();
  }
}




