import { Component, Input, OnInit } from '@angular/core';

import { ViewCell } from 'ng2-smart-table';

@Component({
  template: `
    {{renderValue}}
  `,
})
export class CustomRenderPasswordComponent implements ViewCell, OnInit {

  renderValue: string;

  @Input() value: string | number;

  ngOnInit() {
    this.renderValue = "************";
  }

}

@Component({
  template: `
    {{renderValue}}
  `,
})
export class CustomRenderDateComponent implements ViewCell, OnInit {

  renderValue: string;

  @Input() value: any;

  ngOnInit() {
    let date = new Date(this.value*1000);
    this.renderValue =  date.toLocaleString()
  }

}

@Component({
  template: `
    {{renderValue}}
  `,
})
export class CustomRenderObjectComponent implements ViewCell, OnInit {

  renderValue: string;

  @Input() value: any;

  ngOnInit() {
    this.renderValue = JSON.stringify(this.value)
  }

}




